<!-- @format -->
<template>
    <div ref="videoViewRef" class="players-view">
        <div v-if="videos.length > 0" class="videos-container">
            <div v-for="video in videos" :key="video.id" :class="{ selected: video.selected }">
                <match-video-card
                    :id="video.id"
                    v-model="video.isPlaying"
                    v-waypoint="{
                        active: true,
                        callback: onWaypoint,
                        options: intersectionOptions
                    }"
                    :video="video"
                    @video:delete="spliceVideo($event)"
                />
            </div>
        </div>
    </div>
</template>

<script>
import VideosService from '@/services/videos-service';
import MatchVideoCard from '@/components/videos/match-video-card';
import { eventbus } from '@/main';

export default {
    name: 'Videos',

    components: {
        'match-video-card': MatchVideoCard
    },

    data() {
        return {
            games: [],
            loading: true,
            query: null,
            savedQuery: null,
            intersectionOptions: {
                root: null,
                rootMargin: '0px 0px 0px 0px',
                threshold: 1
            }
        };
    },

    computed: {
        skip: function() {
            return this.videos.length;
        },

        playerId: function() {
            return this.$route.params.id;
        }
    },

    watch: {
        gameId: function() {
            this.videos = [];
            this.queryVideos();
        }
    },

    mounted() {
        this.queryVideos();
        window.addEventListener('scroll', this.handleScroll);
        eventbus.$on('newVideoPosted', this.addedNewVideo);
    },

    beforeDestroy() {
        window.removeEventListener('scroll', this.handleScroll);
        eventbus.$off('newVideoPosted', this.addedNewVideo);
    },

    methods: {
        async queryVideos() {
            var searchQuery = null;

            searchQuery = [
                {
                    queryName: 'GameId',
                    queryValue: this.gameId
                }
            ];

            var queryParameter = {
                skip: this.skip,
                searchQuery: searchQuery
            };

            const response = await VideosService.queryVideos(queryParameter);
            this.hydrateVideos(response);

            if (this.videos.length < 5) {
                this.playFirstVideo();
            }
        },

        playFirstVideo() {
            var count = this.videos.length < 4 ? this.videos.length - 1 : 3;
            for (var i = 0; i <= count; i++) {
                this.videos[i].inview = true;
            }
            this.videos[0].isPlaying = true;
            this.isLoading = false;
        },

        hydrateVideos(response) {
            response.data.videos.forEach(video => {
                this.videos.push({
                    id: video._id,
                    contentType: video.ContentType,
                    contentCreatorId: video.ContentCreatorId,
                    videoType: video.VideoType,
                    url: video.Url,
                    startTime: video.StartTime,
                    endTime: video.EndTime,
                    gameId: video.GameId,
                    match: {
                        player1: {
                            id: video.Player1Id,
                            name: video.Player1.Name,
                            character: {
                                id: video.Player1CharacterId,
                                name: video.Player1Character.Name,
                                imageUrl: video.Player1Character.ImageUrl
                            }
                        },
                        player2: {
                            id: video.Player2Id,
                            name: video.Player2.Name,
                            character: {
                                id: video.Player2CharacterId,
                                name: video.Player2Character.Name,
                                imageUrl: video.Player2Character.ImageUrl
                            }
                        }
                    },
                    tags: video.Tags.map(tag => {
                        return {
                            id: tag._id,
                            name: tag.TagName
                        };
                    }),
                    inview: false,
                    isPlaying: false,
                    isEditing: false
                });
            });
        },

        onWaypoint({ el, going, direction }) {
            var objectId = el.id;
            var featuredVideo = this.videos.find(video => video.id == objectId);
            if (going === this.$waypointMap.GOING_IN && direction) {
                featuredVideo.inview = true;
                featuredVideo.isPlaying = true;
            }

            if (going === this.$waypointMap.GOING_OUT && direction) {
                featuredVideo.isPlaying = false;
            }
        },

        handleScroll() {
            if (this.videos.length > 0) {
                var bottomOfWindow =
                    document.documentElement.scrollTop + window.innerHeight ===
                    document.documentElement.offsetHeight;
                if (bottomOfWindow) {
                    this.queryVideos();
                }
            }
        },

        spliceVideo(video) {
            this.videos.splice(this.videos.indexOf(video), 1);
        },

        addedNewVideo() {
            this.videos = [];
            this.queryVideos();
        }
    }
};
</script>

<style>
.players-view {
    display: flex;
    align-items: flex-start;
    position: relative;
    justify-content: space-around;
    padding-top: 30px;
    height: 100%;
    overflow: hidden;
}

.players-view::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.2);
    border-radius: 10px;
    background-color: #1f1d2b;
}

.players-view::-webkit-scrollbar {
    width: 12px;
    background-color: #1f1d2b;
}

.players-view::-webkit-scrollbar-thumb {
    border-radius: 10px;
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.2);
    background-color: #515b89;
}

.players-view .videos-container {
    position: relative;
    padding: 0 40px;
}

.players-view .videos-container video {
    max-width: 900px;
    margin: 0 auto;
    display: block;
}
</style>
